@import url(https://fonts.googleapis.com/css?family=Lato);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html {
  height: 100%;
}

body {
  height: 100%;
  background: rgb(72, 153, 230);
  display: flex;
  flex-direction: column;
}

#root {
  min-height: 0;
  display: flex;
  flex: 1 1;
}

.container {
  flex: 1 1;
  display: flex;
}

.editorWrap,
.previewWrap {
  display: flex;
  flex-direction: column;
  flex: 1 1; /* 1 = 1 1 0 */
  padding: 1rem;
  overflow-wrap: break-word;
  overflow: auto;
}

.editorWrap textarea {
  resize: none;
}

.toolbar {
  background-color: #7389b8;
  padding: 5px;
  border: 1px solid #1d2f2f;
  box-shadow: 1px 1px 10px 2px #3a4b5f;
  font-family: Lato;
  display: flex;
  justify-content: space-between;
}

#preview,
#editor {
  flex: 1 1;
  background-color: #fffffff1;
  border: 1px solid #1d2f2f;
  box-shadow: 1px 1px 10px 2px #3a4b5f;
  border-top: none;
  padding: 10px;
  overflow: auto;
}

.hide {
  display: none;
}

.toolbar i:hover {
  color: #243c68;
  cursor: pointer;
}

#preview blockquote {
  border-left: 3px solid rgb(34, 48, 75);
  color: rgb(34, 48, 75);
  padding-left: 5px;
  margin-left: 25px;
}

#preview code {
  background: rgb(34, 48, 75);
  color: rgb(226, 226, 226);
  padding: 1px 4px 2px 4px;
  font-size: 12px;
  font-weight: bold;
}

#preview pre {
  background: rgb(34, 48, 75);
  color: rgb(226, 226, 226);
  padding: 5px 0 5px 5px;
  overflow: auto;
}

#preview h1 {
  border-bottom: 2px solid rgb(34, 52, 75);
}

#preview h2 {
  border-bottom: 1px solid rgb(34, 52, 75);
}

#preview table {
  border-collapse: collapse;
  table-layout: fixed;
  width: 100%;
}

#preview ul,
#preview ol {
  padding-left: 20px;
  overflow-wrap: break-word;
}

#preview td,
#preview th {
  border: 2px solid rgb(34, 52, 75);
  padding-left: 5px;
  padding-right: 5px;
}

.footer {
  text-align: center;
  padding: 1rem;
  font-size: 0.8em;
  color: #fff;
  /* flex-shrink: 0; */
}

.footer a {
  color: #fff;
}

.footer a:hover {
  color: #243c68;
}
